/*
 
 PixelRatio.get() === 1          mdpi Android  (160 dpi)
 PixelRatio.get() === 1.5        hdpi Android  (240 dpi)
 PixelRatio.get() === 2          iPhone 4, 4S,iPhone 5, 5c, 5s,iPhone 6,xhdpi Android  (320 dpi)
 PixelRatio.get() === 3          iPhone 6 plus , xxhdpi Android  (480 dpi)
 PixelRatio.get() === 3.5        Nexus 6       */

import {
    Dimensions,
    PixelRatio,
} from 'react-native';


export const SCREEN_WIDTH = Dimensions.get('window').width;      //
export const SCREEN_HEIGHT = Dimensions.get('window').height;    //
let fontScale = PixelRatio.getFontScale();                      //

let pixelRatio = PixelRatio.get();      //
const defaultPixel = 2;                           //iphone6
//pxdp
const w2 = 750 / defaultPixel;
const h2 = 1334 / defaultPixel;
const scale = Math.min(SCREEN_HEIGHT / h2, SCREEN_WIDTH / w2);   //
/**
 * textsp
 * @param size sp
 * return number dp
 */
export function sp(size) {
    size = Math.round((size * scale + 0.5) * pixelRatio / fontScale);
    return size / defaultPixel;
}

export function scaleSize(size) {

    size = Math.round(size * scale + 0.5);
    return size / defaultPixel;
}